.sidebar {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
  width: 100%;
  position: relative;
  z-index: 1;
  margin: 0;
overflow-y: scroll;
}

.sidebar::-webkit-scrollbar{
  display: none;
}

.sidebar-container {
  height: 100vh;
  margin-left: -10%;
}

