.sidebar-header {
  display: flex;
  width: 308px;
  align-items: center;
  height: 10%;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 700;
  font-size: 26px;
  color: #fff;
  margin-left: 20%;
}

.side-menu {
  list-style: none;
  width: 308px;
}

.side-item {
  width: 308px;
  height: 55px;
  display: flex;
  align-items: center;
  border-radius: 10px;
  margin-bottom: 5%;
  box-shadow: none;
}

.side-item:hover {
  width: 308px;
  height: 55px;
  display: flex;
  align-items: center;
  background-color: #00000065;
  border-radius: 10px;
  transition: all 0.2s ease-out;
  box-shadow: none;
}

.is-active {
  width: 308px;
  height: 55px;
  color: #fcd232 !important;
  background-color: #00000065;
  border-radius: 10px;
  transition: all 0.2s ease-out;
  box-shadow: none;
}

.side-links {
  display: flex;
  align-items: center;
  padding-left: 10%;
  width: 308px;
  height: 55px;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 300;
  font-size: 22px;
  text-decoration: none;
  color: #fff;
}

.icons {
  width: 18px;
  height: 18px;
  margin-right: 10px;
  justify-content: center;
  align-items: center;
}

.icons2 {
  width: 16px;
  height: 16px;
  margin-right: 10px;
  justify-content: center;
  align-items: center;
  font-size: 20px;
}
